@font-face {
  font-family: 'brush';
  src: url("DeviantlyBrush.otf") format("opentype");
}

body {
  padding: 0px;
  margin: 0px;
}

.logo-container {
  background-color: #dcd8c0;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.logo-container svg {
  width: 100%;
  height: 100%;
  position: relative;
  bottom: 11%;
  right: 1%;
  transform: rotate(180deg);
}

.logo-container svg text {
  position: absolute;
}

.logo-container text {
  font-family: 'brush';
  stroke-width: 2px;
  stroke: black;
  fill: #dcd8c0;
}

svg text.circle-one {
  font-size: 200px;
  animation: 3s infinite circle-one-animate;
}

.circle-two {
  font-size: 200px;
  animation: 3s infinite circle-two-animate;
}

.l-stroke {
  font-size: 500px;
  animation: 3s infinite l-stroke-animate;
}

@keyframes circle-one-animate {
  0% {
    stroke-dasharray: 0 100%;
    opacity: 1;
  }

  30%, 80% {
    stroke-dasharray: 100% 0;
    opacity: 1;
  }

  90%, 100% {
    opacity: 0;
  }
}

@keyframes circle-two-animate {
  0%, 25% {
    stroke-dasharray: 0 100%;
    opacity: 1;
  }

  55%, 80% {
    stroke-dasharray: 100% 0;
    opacity: 1;
  }

  90%, 100% {
    opacity: 0;
  }
}

@keyframes l-stroke-animate {
  0%, 50%{
    stroke-dasharray: 0 100%;
    opacity: 1;
  }

  80% {
    stroke-dasharray: 100% 0;
    opacity: 1;
  }

  90%, 100% {
    opacity: 0;
  }
}
